@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Merienda:wght@400;500;600;700;800;900&display=swap');
* {

  list-style-type: none;
  
}
img {
  display: block;
  width: 100%;
}
body {
  color: #2f3158;
  padding: 0;
  margin: 0;
  max-width: 95%;
  margin: 0 auto;
  font-family: 'Gilroy', sans-serif;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
::-webkit-scrollbar {
  width: 8px;
  height: 2px;
  background-color: rgb(206, 211, 243);
}
::-webkit-scrollbar-thumb {

  background: #6556e6;  border-radius: 6px;
}